@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600');

@import './global/_main.scss';

@mixin ResetLink(){
    color: inherit;
    text-decoration: none;
}


.icons__social {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    height: 100%;
    
	color: #FAFAFA;

    & > a svg path{
	    fill: #FAFAFA;
    }

    &-item{
    @include ResetLink();
    padding: 1rem;
    display: flex;
    flex-direction: column;
    text-align: center;
    transition: 150ms ease-in-out;
    transform: translateY(0);

        &:hover{
            transform: translateY(-1rem);
        }

        i {
            font-size: 3rem;
        }

        span {
            margin-top: 1rem;
            font-size: 1.8rem;
            font-weight: 300;
        }
    }

}

